body {
  background-color: #edeef0;
  font-family: 'Roboto', sans-serif;
}

.site-wrapper {
  width: 960px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}

.site-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  height: 65px;
}

.site-header__left-side {
  display: flex;
  margin-left: 20px;
}

.site-header__right-side {
  display: flex;
  margin-right: 20px;
}

.site-header__wrapper {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.site-header__text {
  font-size: 16px;
  font-weight: 500;
  margin-left: 5px;
}

.site-header__logo-wrapper {
  margin-right: 20px;
  color: #2e2e2e;
}

.site-header__logo-wrapper:hover {
  color: #ffb343;
  transition: 0.3s;
}

.site-header__logo-icon {
  fill: currentColor;
}

.site-header__search-wrapper {
  display: flex;
  height: 35px;
}

.site-header__search-input {
  width: 350px;
  padding: 5px 10px;
  color: #2e2e2e;
  font-size: 14px;
  border: 2px solid #ffb343;
  border-radius: 3px 0 0 3px;
}

.site-header__search-button {
  width: 85px;
  background-color: #ffb343;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  border-radius: 0 3px 3px 0;
}

.site-header__search-button:hover {
  color: #2e2e2e;
  transition: 0.3s;
}

.site-header__favorite {
  display: flex;
}

.site-header__favorite-wrapper {
  padding: 0 15px;
  color: #2e2e2e;
}

.site-header__favorite-wrapper:hover {
  color: #ffb343;
  transition: 0.3s;
}

.site-header__favorite-icon {
  fill: currentColor;
}

.site-header__add-game {
  display: flex;
}

.site-header__add-game-button {
  display: flex;
  align-items: center;
  padding: 4px 1px 4px 6px;
  border: 2px solid #ffb343;
  border-radius: 3px;
  color: #ffb343;
  background-color: #fff;
}

.site-header__add-game-button:hover {
  color: #fff;
  background-color: #ffb343;
  transition: 0.3s;
}

.site-header__add-game-icon {
  fill: currentColor;
}

.content__games {
  margin-top: 20px;
  padding: 15px 7px;
  border-radius: 3px;
  background-color: #fff;
}

.content__title {
  font-size: 16px;
  font-weight: 400;
  padding: 0 7px;
}

.content__games-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 5px;
}

.content__game-new {
  padding: 3px 7px 0 7px;
  color: #fff;
  font-size: 8px;
  background-color: #ffb343;
  border-radius: 3px;
}

.content__game-rating {
  display: flex;
}

.content__game-rating-star {
  margin-right: 3px;
  fill: #c9c9c9;
}

.content__console {
  display: flex;
  align-items: center;
}

.content__console-icon {
  margin-right: 3px;
  fill: #c9c9c9;
}

.content__buy-game-button {
  display: flex;
  align-items: center;
  padding: 0 6px;
  background-color: transparent;
  border: 2px solid #ffb343;
  border-radius: 3px;
  font-size: 12px;
  font-weight: 700;
  color: #ffb343;
}

.content__buy-game-button:hover {
  color: #fff;
  background-color: #ffb343;
  transition: 0.3s;
}

.content__game-price {
  display: flex;
}

.content__game-price::after {
  content: "\20BD";
}

.content__games-popular {
  margin-top: 20px;
  font-size: 14px;
}

.content__games-new {
  margin-top: 20px;
  font-size: 14px;
}

.content__games-all {
  font-size: 14px;
}

.content__games-all-item {
  width: 175px;
  height: 330px;
  margin: 0 7px 15px 7px;
  border-radius: 3px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
}

.content__games-all-item:hover {
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.15);
}

.content__games-all-item-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 3px 3px 0 0;
}

.content__games-all-item-cover-wrapper {
  height: 190px;
}

.content__games-all-item-info-wrapper {
  padding: 10px;
}

.content__games-all-item-header {
  display: flex;
  justify-content: space-between;
  height: 15px;
}

.content__games-all-item-content {
  display: flex;
  height: 80px;
}

.content__games-all-item-description {
  margin: auto 0;
}

.content__games-all-item-title {
  font-size: 16px;
  font-weight: 700;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.content__games-all-item-text {
  height: 45px;
  font-size: 9.5px;
  overflow: hidden;
}

.content__games-all-item-footer {
  display: flex;
  justify-content: space-between;
  height: 25px;
}

